.Paginator ul {
   list-style: none;
   display: flex;
   text-align: center;
}

.Paginator a {
   display: block;
   min-width: 25px;
   height: 25px;
   padding: 10px;
   line-height: 25px;
   border: 1px solid rgb(163, 163, 163);
   text-decoration: none;
   color: white;
   background-color: rgb(65, 65, 65);
}

.active a {
   background-color: rgb(54, 54, 54);
   color: gold;
}

.disabledClass a {
   cursor: context-menu;
   background-color: rgb(116, 116, 116);
}